
<div class="easy-display-header" :style="{ '--theme-color': themeColor }">
  <div class="logo-part">
    <img
      :src="logo"
      alt="logo"
      :style="{ width: logoSize, height: logoSize }"
    />
  </div>
  <div class="main-part">
    <div class="title-label">
      <div
        class="title"
        :style="{
          fontSize: titleFontSize,
          color: textColor,
        }"
        v-text="title"
      ></div>
      <div
        class="desp"
        :style="{
          color: textColor,
        }"
        style="opacity: 0.7"
        v-text="desp"
      ></div>
    </div>
    <div class="date-label">
      <div class="date-box">
        <div
          class="day"
          :style="{
            color: textColor,
          }"
          v-text="getDay()"
        ></div>
        <div
          class="date"
          :style="{
            color: textColor,
          }"
          v-text="date"
        ></div>
      </div>
      <div class="separator"></div>
      <div
        class="time"
        :style="{
          color: textColor,
          fontSize: timeFontSize,
        }"
        style="font-weight: bold"
        v-text="time"
      ></div>
    </div>
  </div>
</div>
